import { useEffect, useRef } from "react";
import "../css/SlickScroll.css";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Home from "./Home";
import { Link, Route } from "react-router-dom";
import useScrollFadeIn from "./useScrollFadeIn";
import WebFooter from "./MainFooter";
import EmailApi from "./EmailApi";
const DIVIDER_HEIGHT = 5;

const SlickScroll = () => {
  const outerDivRef = useRef();
  useEffect(() => {
    const wheelHandler = e => {
      e.preventDefault();
      const { deltaY } = e;
      const { scrollTop } = outerDivRef.current; //    
      const pageHeight = window.innerHeight; //  , 100vh .
      if (deltaY > 0) {
        //   
        if (scrollTop >= 0 && scrollTop < pageHeight) {
          // 1
          console.log(" 1, pageHeight");

          outerDivRef.current.scrollTo({
            top: pageHeight + DIVIDER_HEIGHT,
            left: 0,
            behavior: "smooth",
          });
        } else if (scrollTop >= pageHeight && scrollTop < pageHeight * 2) {
          // 2
          console.log(" 2, down");
          outerDivRef.current.scrollTo({
            top: pageHeight * 2 + DIVIDER_HEIGHT * 2,
            left: 0,
            behavior: "smooth",
          });
        } else if (scrollTop >= pageHeight && scrollTop < pageHeight * 3) {
          //  3
          console.log(" 3, down");
          outerDivRef.current.scrollTo({
            top: pageHeight * 3 + DIVIDER_HEIGHT * 3,
            left: 0,
            behavior: "smooth",
          });
        } else if (scrollTop >= pageHeight && scrollTop < pageHeight * 4) {
          console.log(" 4, down");
          outerDivRef.current.scrollTo({
            top: pageHeight * 4 + DIVIDER_HEIGHT * 4,
            left: 0,
            behavior: "smooth",
          });
        } else {
          console.log(" 5, down");
          outerDivRef.current.scrollTo({
            top: pageHeight * 4 + DIVIDER_HEIGHT * 4,
            left: 0,
            behavior: "smooth",
          });
        }
      } else {
        //   
        if (scrollTop >= 0 && scrollTop < pageHeight) {
          // 1
          console.log(" 1, up");
          outerDivRef.current.scrollTo({
            top: 0,
            left: 0,
            behavior: "smooth",
          });
        } else if (scrollTop >= pageHeight && scrollTop < pageHeight * 2) {
          // 2
          console.log(" 2, up");
          outerDivRef.current.scrollTo({
            top: 0,
            left: 0,
            behavior: "smooth",
          });
        } else if (scrollTop >= pageHeight && scrollTop < pageHeight * 3) {
          //  3
          console.log(" 3, up");
          outerDivRef.current.scrollTo({
            top: pageHeight + DIVIDER_HEIGHT,
            left: 0,
            behavior: "smooth",
          });
        } else if (scrollTop >= pageHeight && scrollTop < pageHeight * 4) {
          //  3
          console.log(" 4, up");
          outerDivRef.current.scrollTo({
            top: pageHeight * 2 + DIVIDER_HEIGHT * 2,
            left: 0,
            behavior: "smooth",
          });
        } else {
          //  3
          console.log(" 5, up");
          outerDivRef.current.scrollTo({
            top: pageHeight * 3 + DIVIDER_HEIGHT * 3,
            left: 0,
            behavior: "smooth",
          });
        }
      }
    };
    const outerDivRefCurrent = outerDivRef.current;
    outerDivRefCurrent.addEventListener("wheel", wheelHandler);
    return () => {
      outerDivRefCurrent.removeEventListener("wheel", wheelHandler);
    };
  }, []);
  return (
    <div ref={outerDivRef} className="outer">
      <div>
        <Home />
      </div>
      <div
        className="inner bg-blue"
        // {...useScrollFadeIn("up", 1, 0)} <-   
      >
        <ul>
          <li {...useScrollFadeIn("up", 1, 0)}>
            <Link to="/page2">
              <img src="./simmons1.jpg" alt="" />
            </Link>
          </li>

          <li {...useScrollFadeIn("up", 1, 0)}>
            <Link to="/page2">
              <img src="./simmons2.jpg" alt="" />
            </Link>
          </li>
          <li {...useScrollFadeIn("up", 1, 0)}>
            <Link to="/page2">
              <img src="./simmons3.jpg" alt="" />
            </Link>
          </li>
          <li {...useScrollFadeIn("up", 1, 0)}>
            <Link to="/page2">
              <img src="./simmons4.jpg" alt="" />
            </Link>
          </li>
        </ul>
      </div>
      <div className="inner bg-pink">
        <div className="span" {...useScrollFadeIn("up", 1, 0)}>
          <h1>  </h1>
          <span>        </span>
        </div>
        <video loop autoPlay muted playsInline {...useScrollFadeIn("up", 1, 0)}>
          <source src="./__ - 85002.mp4" type="video/mp4" />
        </video>
        <div className="span2" {...useScrollFadeIn("up", 1, 0)}>
          <h1>  </h1>
          <span>
               100%     
            <br />
               
          </span>
        </div>
      </div>
      <div className="inner bg-black">
        <EmailApi />
      </div>
      <div className="inner bg-dark">
        <li {...useScrollFadeIn("up", 1, 0)}>
          <Link to="/ApiLandingPage">
            <img src="./store-map.jpg" alt="" />
          </Link>
        </li>
      </div>
      <div className="Footer">
        <WebFooter />
      </div>
    </div>
  );
};

export default SlickScroll;
